.navbar{
  background: var(--primaryNav-color);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar__container{
  display: flex;
  justify-content: space-between;
  height: 80px;
  z-index: 1;
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 50px;
}

.navbar__menu{
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
}

.navbar__item{
  height: 50px
}

.navbar__links{
  color: var(--primaryText-color);
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 0 1rem;
  height: 100%;
}


.PreviewCards__card {
  height: 525px;
  width: 400px;
  border-radius: 4px;
  background-image: linear-gradient(
  to bottom, 
  rgba(0,0,0,0) 0%, 
  rgba(17,17,17,0.6) 100%), 
  url('images/Powerlines.jpg');
  background-size: cover;
  position: relative;
  color: #fff;
  margin: 30px;
  text-align: center;
}

.PreviewCards__card:nth-child(2){
  background-image: linear-gradient(
  to bottom, 
  rgba(0,0,0,0) 0%, 
  rgba(17,17,17,0.6) 100%), 
  url('images/BridgeStraight.jpg');
}

.PreviewCards__card:nth-child(3){
  background-image: linear-gradient(
  to bottom, 
  rgba(0,0,0,0) 0%, 
  rgba(17,17,17,0.6) 100%), 
  url('images/Meadow.jpg');
}

.PreviewCards h3{
  margin-left: auto;
  margin-right: auto;
  position: relative;
  margin-top: 450px;
  font-size: xx-large;
  color: darkgray;
}

.PreviewCards__container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.main__container{
  background-color: rgb(92, 105, 83);
  height: 300vh;
  text-align: center;
  margin: 0px;
}

.PreviewCards__card:hover{
  transform: scale(1.075);
  transition: 0.5s ease-in-out;
}

.PreviewCards__card:not(:hover){
  transform: scale(1);
  transition: 0.5s ease-in-out;
}




.VideoGallery__container{
  display: contents;
  justify-content: center;
  flex-wrap: wrap;
}

.VideoGallery__Vid{
  margin: 30px;
}

.VideoCard:hover{
  transform: scale(1.075);
  transition: 0.5s ease-in-out;
}

.VideoCard:not(:hover){
  transform: scale(1);
  transition: 0.5s ease-in-out;
}